import React, { PureComponent } from 'react';
import styled, { keyframes } from 'styled-components';
import { Container, Row, Col } from 'react-bootstrap';
import scrollTo from '../lib/scroll-into-view';

// components
import { Button } from './inputs';

// assets
import logoLeft from '../images/jk-logo-black.svg';
import logoRight from '../images/bracket-black.svg';

/**
 * Styled components
 */
const LogoLeft = styled.img`
  position: relative;
  float: left;
  top: 0;
  width: 42px;
  height: 45px;
`;

const LogoRight = styled.img`
  position: relative;
  float: left;
  top: 0;
  left: -18px;
  width: 42px;
  height: 45px;
  opacity: 0;
  transition: all 0.5s ease-out;
`;

const Title = styled.h2`
  position: relative;
  float: left;
  left: -36px;
  color: #000;
  font-family: 'Lato', sans-serif;
  font-size: 18px;
  white-space: nowrap;
  overflow: hidden;
  width: 176px;
  margin: 12px 0 0 0;
  opacity: 0;
`;

// TODO: DRY
const typeAnimation = keyframes`
  0% {
    opacity: 0;
    width: 0;
  }
  50% {
    opacity: 1;
    width: 0;
  }
  100% {
    opacity: 1;
    width: 100;
  }
`;

const NavLogo = styled.div`
  width: 42px;
  height: 45px;
  margin: 12px 0 0 0;
  overflow: hidden;
  cursor: pointer;
  background: none;

  @media (min-width: 768px) {
    &:hover {
      width: 268px;
    }

    &:hover ${LogoRight} {
      opacity: 1;
      transform: translate3d(176px, 0, 0);
    }

    &:hover ${Title} {
      opacity: 1;
      animation: ${typeAnimation} 2s steps(30, end);
    }
  }
`;

// TODO: DRY
const blinker = keyframes`
  to {
    opacity: .0;
  }
`;

const Blink = styled.span`
  margin: 0 0 0 4px;
  animation: ${blinker} 1.25s infinite;
`;

const Menu = styled.ul`
  list-style: none;
  margin: 22px 0 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: flex-end;
`;

const MenuItem = styled.li`
  width: 100px;
  text-align: right;
  font-family: 'Lato', sans-serif;
  font-size: 14px;

  @media (min-width: 768px) {
    font-size: 18px;
  }
`;

const renderMenuLink = ({ label, href, options = {} }, index) => (
  <MenuItem key={`menu-${index}`}>
    <Button
      type="hyperlink"
      href={href}
      onClick={e => {
        e.preventDefault();
        scrollTo(href.replace('#', ''), 800, options);
      }}
    >
      {label}
    </Button>
  </MenuItem>
);

class Navigation extends PureComponent {
  render() {
    const { metaData, title, links } = this.props;
    return (
      <nav className="nerve" id="navbar">
        <Container>
          <Row>
            <Col xs={3}>
              <NavLogo>
                <Button
                  type="hyperlink"
                  href="#intro"
                  onClick={e => {
                    e.preventDefault();
                    scrollTo('intro', 800, true);
                  }}
                >
                  <LogoLeft src={logoLeft} alt={metaData.logoLeft} />
                  <LogoRight src={logoRight} alt={metaData.logoRight} />
                </Button>
                <Title>
                  {title}
                  <Blink>|</Blink>
                </Title>
              </NavLogo>
            </Col>
            <Col xs={9}>
              <Menu>{links.map(renderMenuLink)}</Menu>
            </Col>
          </Row>
        </Container>
      </nav>
    );
  }
}

export default Navigation;
