import React from 'react';
import { Container, Row, Col, Modal } from 'react-bootstrap';
import Recaptcha from 'react-recaptcha';
import styled from 'styled-components';

const Footer = () => {
  const [modalShow, setModalShow] = React.useState(false);

  const forHumansOnly = () => {
    const spread = 'jam';
    const badBrowser = 'ie';
    const uri = 'gjyllek';
    const commerce = 'com';
    const letter = `${spread + badBrowser}@${uri
      .split('')
      .reverse()
      .join('')}.${commerce}`;
    const humansOnly = document.getElementById('captcha');
    humansOnly.innerHTML = letter;
    setModalShow(false);
  };

  return (
    <footer id="footer">
      <div id="oj">
        <p className="text-center">
          <i className="icon icon-copyright"></i>
          <span>Jamie Kelly</span>
        </p>
      </div>
      <Container>
        <Row>
          <Col lg={{ span: 8, offset: 1 }}>
            <div id="contact-info">
              <p>
                Interested in working together?
                <br />
                Email me at&nbsp;
                <span
                  id="captcha"
                  onClick={() => setModalShow(true)}
                  onKeyDown={() => setModalShow(true)}
                  role="button"
                  tabIndex={0}
                >
                  [click to reveal]
                </span>
              </p>
              <div id="social">
                <a
                  href="https://github.com/j-quelly"
                  target="_blank"
                  rel="noopener noreferrer nofollow"
                >
                  <i className="icon icon-github"></i>
                </a>
                <a
                  href="https://ca.linkedin.com/in/jamie-kelly-02652721"
                  target="_blank"
                  rel="noopener noreferrer nofollow"
                >
                  <i className="icon icon-linkedin"></i>
                </a>
                <a
                  href="https://twitter.com/j_quelly"
                  target="_blank"
                  rel="noopener noreferrer nofollow"
                >
                  <i className="icon icon-twitter"></i>
                </a>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
      <MyVerticallyCenteredModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        verifyCallback={forHumansOnly}
      />
    </footer>
  );
};

const TextCenter = styled.div`
  text-align: center;
`;

const MyVerticallyCenteredModal = ({ verifyCallback, ...props }) => (
  <Modal
    {...props}
    size="sm"
    aria-labelledby="contained-modal-title-vcenter"
    centered
  >
    <Modal.Body>
      <TextCenter>
        <Recaptcha
          className="g-recaptcha"
          sitekey="6LehlQ4UAAAAAJ8HqM4-7LyKLg7XkoNSoBoXtVEd"
          verifyCallback={verifyCallback}
        />
      </TextCenter>
    </Modal.Body>
  </Modal>
);

export default Footer;
